.status {
  margin: 0.5em 0 0.5em 4em;
  padding: 1em 2em;
  background: $status-background-color;
  // background-position: 18px 1em;
  // background-repeat: no-repeat;
  // border: 1px solid $status-border-color;
  position: relative;
  // added break-word for email wrapping
  word-wrap: break-word;
}

.status:before {
  display: block;
  content: "";
  position: absolute;
  top: 0;
  margin-left: -2.4em;
  width: 1em;
  height: 1em;
  text-align: center;
  color: $black;
  padding: 0.4em;
}

/* status in butterfly lightbox */
#jb-window .status {
  margin: 0;
}

.status h2 {
  margin-top: 0;
}

@mixin status($icon: $fa-var-info-circle, $background-color: $info-background-color, $color: $info-color, $border-color: $info-border-color) {
  background-color: $background-color;
  color: $color;
  @include fonticon($icon, 1, 2.5, 1, #000);

  &:before, &:hover:before {
    background: $background-color;
    color: $border-color;
  }
}

.info, .status {
  @include status($fa-var-info-circle, $info-background-color, $info-color, $info-border-color);
  clear:both;
}

.success-step {
  @include status($fa-var-check, $success-step-background-color, $success-step-color, $success-step-border-color);
}

.success {
  @include status($fa-var-check-circle, $success-background-color, $success-color, $success-border-color);
}

.warn {
  @include status($fa-var-exclamation-triangle, $warn-background-color, $warn-color, $warn-border-color);
}

.fail {
  @include status($fa-var-ban, $fail-background-color, $fail-color, $fail-border-color);
}

div.download {
  @include status($fa-var-download, $download-background-color, $download-color, $download-border-color);
}